import React, { Component } from 'react'
import FormField from './FormField';
// import FormGroup from './FormGroup'

class Form extends Component {
  render() {
    return (
      <form className="form-block">
        {this.props.children?(
          <div className="form-block__heading">{this.props.children}</div>
        ):null}
        <div className="form-block__body">
          <div className="form-block__group">
            {this.props.data?Object.keys(this.props.data).map((key, index) => (
              <FormField key={key} field={this.props.data[key]}/>
            )):null}
          </div>
        </div>
      </form>
    )
  }
}

export default Form;
