import React, { Component } from 'react'
import { Button, Pane, PlusIcon, LinkIcon, SideSheet, FloppyDiskIcon, Heading } from 'evergreen-ui'
import TableCmp from './TableCmp';
import Form from './Form';

class FormRepeater extends Component {
  constructor(props)
  {
    super(props)
    var fieldData = {}
    fieldData[this.props.field.key] = this.props.field
    // TODO: replace field ref to type lookup ( using this.props.field.repeater )
    this.state = {
      defaultValue: [],
      showAddRow: false,
      isNewField: true,
      fieldData: fieldData
      // field: props.field
    }
    // if (props.field.value === undefined) this.props.field.value = this.props.defaultValue;
    this.handleChange = this.handleChange.bind(this)
  }
  handleChange(change)
  {
    // this.setState({
    //   field: { ...this.props.field, value: change.values }
    // })
    // this.props.onChange(this.props.field.key, change.values)
  }
  render() {
    return (
      <div className="form-block__field">
        <Pane display="flex" justifyContent="space-between" borderBottom="muted">
          <Pane>
            <label className="form-block__field-label">{
              this.props.field.key.startsWith("@")?(
                <LinkIcon size={10} color="selected" marginRight=".5em"/>
              ):null
            }{this.props.field.label}</label>
            {(this.props.field.description?(
              <small className="form-block__field-describe">{this.props.field.description}</small>
            ):null)}
          </Pane>
          <Pane marginBottom=".5em">
            <Button appearance="minimal" iconBefore={PlusIcon} onClick={(e) => { e.preventDefault(); this.setState({ showAddRow: true })}}>Add {this.props.field.label}</Button>
          </Pane>
        </Pane>
        <Pane>
          <TableCmp emptyMsg={`No ${this.props.field.label}`} cols={this.props.field?.columns} rows={this.props.field.value || []} />
        </Pane>
        <SideSheet
          isShown={this.state.showAddRow}
          onCloseComplete={() => this.setState({ showAddRow: false })}
        >
          <Form className="split-content__form" onChange={this.handleChange} data={this.state.fieldData}>
            <Pane display="flex" padding="1em" background="tint2" borderBottom="muted">
              <Pane flex={1} alignItems="center" display="flex">
                <Heading size={600}>{`${this.state.isNewField?'Add':'Edit'} ${this.props.field.label}`}</Heading>
              </Pane>
              <Pane>
                <Button appearance="minimal" iconBefore={FloppyDiskIcon}>{this.state.isNewField?'Save':'Update'}</Button>
              </Pane>
            </Pane>
          </Form>
        </SideSheet>
      </div>
    )
  }
}

export default FormRepeater;
