import React, { Component } from 'react'
import { TagInput } from 'evergreen-ui'

class FormStringArray extends Component {
  constructor(props)
  {
    super(props)
    this.handleChange = this.handleChange.bind(this)
  }
  handleChange(change)
  {
    this.props.field.onChange(this.props.field.key, change.values)
  }
  render() {
    return (
      <div className="form-block__field">
        <label className="form-block__field-label">{this.props.field.label}</label>
        {(this.props.field.description?(
          <small className="form-block__field-describe">{this.props.field.description}</small>
        ):null)}
        <TagInput
          inputProps={{ placeholder: this.props.field.placeholder || 'Type here...' }}
          values={this.props.field.value}
          onChange={values => {
            this.handleChange({ values })
          }}
        />
      </div>
    )
  }
}

export default FormStringArray;
