import React, { Component } from 'react'
import { Checkbox } from 'evergreen-ui'

class FormCheckbox extends Component {
  constructor(props)
  {
    super(props)
    this.handleChange = this.handleChange.bind(this)
  }
  handleChange(event)
  {
    this.props.field.onChange(this.props.field.key, event.target.value)
  }
  render() {
    return (
      <div className="form-block__field">
        <label className="form-block__field-label">{this.props.field?.label}</label>
        {(this.props.field?.description?(
          <small className="form-block__field-describe">{this.props.field?.description}</small>
        ):null)}
        <ul className="form-block__field">
          {this.props.field.options?this.props.field.options.map((opt, index) => (
            <li key={index}>
              <label>
                <Checkbox
                  checked={this.props.field.value}
                  onChange={this.handleChange}
                />
                <span className="form-block__field-value">
                  {opt}
                </span>
              </label>
            </li>
          )):null}
        </ul>
      </div>
    )
  }
}

export default FormCheckbox;
