import React, { Component } from 'react'
import FormCheckbox from './FormCheckbox'
import FormBoolean from './FormBoolean'
import FormKeys from './FormKeys'
import FormText from './FormText'
import FormSelect from './FormSelect'
// import FormMultiSelect from './FormMultiSelect'
import FormStringArray from './FormStringArray'
// import FormString from './FormString'
import FormTextArea from './FormTextArea'
import FormRepeater from './FormRepeater'

class FormField extends Component {
  constructor(props)
  {
    super(props)
    this.state = {
      // key: null,
      // label: false,
      // description: false,
      // errors: [],
      // type: 'text',
      // field: {
      //   key: null,
      //   label: false,
      //   description: false,
      //   errors: [],
      //   type: 'text',
      //   ...props.field
      // }
    }
  }
  getFieldType(type) 
  {
    switch(type)
    {
      case 'checkbox':
        return (<FormCheckbox field={this.props.field}/>)
      case 'boolean':
        return (<FormBoolean field={this.props.field}/>)
      case 'keys':
        return (<FormKeys field={this.props.field}/>)
      case 'text':
        return (<FormText field={this.props.field}/>)
      case 'textarea':
        return (<FormTextArea field={this.props.field}/>)
      case 'select':
        return (<FormSelect field={this.props.field}/>)
      // case 'multiselect':
      //   return (<FormMultiSelect field={this.props.field}/>)
      case 'string[]':
        return (<FormStringArray field={this.props.field}/>)
      case 'repeater':
        return (<FormRepeater field={this.props.field}/>)
      default:
        return (<>
          {(this.props.field.label?(
            <label className="form-block__field-label">{this.props.field.label}</label>
          ):null)}
          {(this.props.field.description?(
            <small className="form-block__field-describe">{this.props.field.description}</small>
          ):null)}
        </>)
    }
  }
  render() {
    return (
      <div className={`form-field${this.props.field.errors.length?' --error':''}`}>
        {this.getFieldType(this.props.field.type)}
      </div>
    )
  }
}

export default FormField;
