import React, { Component } from 'react'
import { BrowserRouter as Router, Route, NavLink } from 'react-router-dom'
import { Pane, Button, Popover, Position, Menu, CubeAddIcon, GlobeIcon, CodeIcon, BuildIcon, CaretDownIcon, Dialog, Heading, toaster } from 'evergreen-ui'
// import { useAuth0 } from "@auth0/auth0-react";
// import { Auth0Context } from "@auth0/auth0-react";

import '../@styles/index.scss';

// import Header from './Header';
import Brand from '../@components/Brand';
import ConfigService from '../@services/Config';
import PageOverview from './Overview';
import PageConfig from './Config';
import PageTemplates from './Templates';
import PageTools from './Tools';
import Busy from '../@components/Busy';
import Form from '../@components/Form';

class Root extends Component {
  
  // static contextType = Auth0Context;

  constructor(props)
  {
    super(props)
    this.state = {
      busy: true,
      addNewBusy: false,
      isAddNewShown: false,
      currentSite: false,
      sites: [],
      formData: {}
    }
    this.setSite = this.setSite.bind(this)
    this.handleChange = this.handleChange.bind(this)
  }

  componentDidMount()
  {

    this.mounted = true;
    
    this.getSites(window.location.pathname.split('/')[1])
      .then(()=>{

        this.populateSiteSchema()

      })

  }

  populateSiteSchema()
  {
    
    ConfigService.getSiteSchema()
      .then(({ documentation, schema, supportsTemplates, table }) => {

        if (!this.mounted) return;

        var SchemaFormValue = {}

        Object.keys(schema).forEach(key => {

          SchemaFormValue[key] = {
            ...schema[key],
            key: key,
            errors: [],
            onChange: (key, value) => {
              this.setState({
                formData: {...this.state.formData, [key]: { ...this.state.formData[key], value: value }}
              })
            },
            value: (() => { return schema[key].default || undefined })()
          }

        })

        this.setState({
          formData: SchemaFormValue,
          busy: false
        })

      })
  }

  getSites(currentSiteKey)
  {
    return new Promise((resolve, reject) => {

      ConfigService.getSites()
        .then(json => {

          var currentSite = json.filter(item => currentSiteKey === item.key)[0]
          
          ConfigService.site = currentSite

          this.setState({
            sites: json,
            currentSite: currentSite || false
          })

          resolve()

        })
    
    })

  }

  setSite(site)
  {
    // TODO: cache
    ConfigService.site = site
    if (!site)
    {
      this.setState({
        currentSite: false
      })
    } else {
      this.setState({
        currentSite: site
      })
      // ConfigService.getSiteConfig(site)
      //   .then(config => {
      //     this.setState({
      //       currentSite: site
      //     })
      //   })
    }
  }

  handleChange(value)
  {
    console.log("root_change", value)
    //
  }

  render() {

    return (
      <main>
        {
          this.state.busy?(
            <Busy/>
          ):(<Router>
              <header>
                <nav className="nav nav--left">
                  <a href="/wp-admin" className="nav__item"><Brand/></a>

                  <div className="nav__item">
                    <Popover
                      position={Position.BOTTOM_LEFT}
                      content={({ close }) => (
                        <Menu>
                          <Menu.Group>
                            {this.state.sites.map((item, index)=>(
                              <NavLink key={item.key} className="nav__item" activeClassName='--active' to={`/${item.key}`} onClick={() => { this.setSite(item); close() }}>
                                <Menu.Item disabled={this.state.currentSite?.key === item.key}>{`${item.name}`}</Menu.Item>
                              </NavLink>
                            ))}
                          </Menu.Group>
                          <Menu.Divider />
                          <Menu.Group>
                            <Menu.Item>
                              <NavLink className="nav__item" to="/" onClick={() => { this.setSite(false); close() }}>
                                Sites Overview
                              </NavLink>
                            </Menu.Item>
                            <Menu.Item onSelect={() => this.setState({ isAddNewShown: true })}>
                              Add New Site
                            </Menu.Item>
                          </Menu.Group>
                        </Menu>
                    )}>
                      <Button appearance="minimal">{this.state.currentSite ? (
                        <>
                          {this.state.currentSite.name}
                          <CaretDownIcon/>
                        </>
                      ) : 'Select Site'}</Button>
                    </Popover>

                  </div>
                  {this.state.currentSite ? (
                    <>
                      <NavLink className="nav__item" activeClassName='--active' exact={true} to={`/${this.state.currentSite?.key}`}>
                        <Button appearance="minimal"><GlobeIcon marginRight={5}/> Settings</Button>
                      </NavLink>
                      <NavLink className="nav__item" activeClassName='--active' to={`/${this.state.currentSite?.key}/config`}>
                        <Button appearance="minimal"><CodeIcon marginRight={5}/> Config</Button>
                      </NavLink>
                      <NavLink className="nav__item" activeClassName='--active' to={`/${this.state.currentSite?.key}/tools`}>
                        <Button appearance="minimal"><BuildIcon marginRight={5}/> Tools</Button>
                      </NavLink>
                      <NavLink className="nav__item" activeClassName='--active' to={`/${this.state.currentSite?.key}/templates`}>
                        <Button appearance="minimal"><CubeAddIcon marginRight={5}/> Templates</Button>
                      </NavLink>
                    </>
                  ) : null}
                </nav>
              </header>
              {this.state.currentSite?(
                <>
                  <Route path={`/:siteKey`} exact={true} component={props=>(<PageOverview {...props} currentSite={this.state.currentSite} />)}/>
                  <Route path={`/:siteKey/config`} component={PageConfig}/>
                  <Route path={`/:siteKey/templates`} component={PageTemplates}/>
                  <Route path={`/:siteKey/tools`} component={PageTools}/>
                </>
              ):(
                <Pane display="flex" flexWrap="wrap">
                {
                  this.state.sites.map((item, index)=>(
                    <Pane className="" key={index} flex="0 0 calc(25% - 2em)" borderRadius={5} height={128} display="flex" justifyContent="center" alignItems="center" margin="1em" elevation={1} border="default">
                      <NavLink to={`/${item.key}`} onClick={() => { this.setSite(item) }}>
                        <Heading size={500}>{item.name}</Heading>
                      </NavLink>
                    </Pane>
                  ))
                }
                </Pane>
              )}
              <Dialog
                isShown={this.state.isAddNewShown}
                hasHeader={false}
                intent="success"
                isConfirmLoading={this.state.addNewBusy}
                onConfirm={() => {
                  this.setState({ addNewBusy: true })
                  ConfigService.addSite(this.state.formData)
                    .then(res => {

                      this.getSites(res.key)
                        .then(()=>{

                          this.setState({ isAddNewShown: false, addNewBusy: false })
                          toaster.success('Added Successfully', { description: 'Connect your source to a destination to receive data.' })
                          window.location.href=`/${res.key}/`

                        })
                        
                    })
                }}
                onCloseComplete={() => this.setState({ isAddNewShown: false, addNewBusy: false })}
                confirmLabel="Save"
              >
                <Heading size={600} marginBottom={5}>Add Site</Heading>
                <Form className="split-content__form" onChange={this.handleChange} data={this.state.formData}/>
              </Dialog>
            </Router>)
        }
      </main>
    )
  }
}

export default Root;
