
class ConfigServiceClass {

  // host = 'https://wp-factory-api.herokuapp.com'
  host = 'https://wp-factory-api.herokuapp.com'
  version = 'v1'
  site = false

  get(path)
  {
    return new Promise((resolve, reject) => {
      
      fetch(`${this.host}/api/${this.version}${path}`, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json'
        }
      })
      .then(res => res.json())
      .then(json => {
        
        resolve(json)

      })
      .catch(e => reject(e))

    })
  }

  post(path, data)
  {
    return new Promise((resolve, reject) => {
      
      fetch(`${this.host}/api/${this.version}${path}`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(data)
      })
      .then(res => res.json())
      .then(json => {
        
        resolve(json)

      })
      .catch(e => reject(e))

    })
  }

  delete(path)
  {
    return new Promise((resolve, reject) => {
      
      fetch(`${this.host}/api/${this.version}${path}`, {
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json'
        }
      })
      .then(res => res.json())
      .then(json => {
        
        resolve(json)

      })
      .catch(e => reject(e))

    })
  }

  normalizeFormData(data)
  {
    var $Data = {};
    Object.keys(data).forEach(key => {
      if (data[key].value !== undefined)
        $Data[key] = data[key].value;
    })
    return $Data;
  }

  getSites()
  {
    return this.get('/sites')
  }

  addSite(siteData)
  {
    return this.post('/sites', this.normalizeFormData(siteData))
  }

  deleteSite(siteKey)
  {
    return this.delete(`/sites/${siteKey}`)
  }

  setActiveSite(site)
  {
    this.getConfig()
    this.site = site
  }

  // window.addEventListener('storage', function(e) {

  // })

  /**
   * retrieve and cache @config key schema
   * @returns "@Config" object schema by key
   */
  getSiteSchema()
  {
    return new Promise((resolve, reject) => {
      if (!localStorage.getItem(`@schema/sites`))
        this.get(`/sites/schema`).then(json => {
          localStorage.setItem(`@schema/sites`, JSON.stringify(json))
          resolve(json)
        }).catch(e => reject(e))
      else
        resolve(JSON.parse(localStorage.getItem(`@schema/sites`)))
    })
  }

  /**
   * retrieve and cache @config key schema
   * @returns "@Config" object schema by key
   */
  getSchema(key)
  {
    return new Promise((resolve, reject) => {
      if (!localStorage.getItem(`@schema/${key}`))
        this.get(`/sites/${this.site.key}/config/${key}/schema`).then(json => {
          localStorage.setItem(`@schema/${key}`, JSON.stringify(json))
          resolve(json)
        }).catch(e => reject(e))
      else
        resolve(JSON.parse(localStorage.getItem(`@schema/${key}`)))
    })
  }

  /**
   * Retrieve and cache saved @config json
   * @returns "@Config" object
   */
  getConfig()
  {
    return new Promise((resolve, reject) => {
      if (!localStorage.getItem(`@config/${this.site.key}`))
        this.get(`/sites/${this.site.key}/config`)
          .then(json => {
            localStorage.setItem(`@config/${this.site.key}/config`, JSON.stringify(json))
            Object.keys(json).forEach(key => {
              localStorage.setItem(`@config/${this.site.key}/config/${key}`, JSON.stringify(json[key]))
            })
            resolve(json)
          })
          .catch(e => reject(e))
      else
        resolve(JSON.parse(localStorage.getItem(`@config/${this.site.key}`)))
    })
  }

  /**
   * retrieve and cache @config key value
   * @returns "@Config" object key by key
   */
  getValue(key)
  {
    return new Promise((resolve, reject) => {
      if (!localStorage.getItem(`@config/${this.site.key}/config/${key}`))
        this.get(`/sites/${this.site.key}/config/${key}`).then(json => {
          localStorage.setItem(`@config/${this.site.key}/config/${key}`, JSON.stringify(json))
          resolve(json)
        }).catch(e => reject(e))
      else
        resolve(JSON.parse(localStorage.getItem(`@config/${this.site.key}/config/${key}`)))
    })
  }

  setValue(key, fieldData)
  {
    return new Promise((resolve, reject) => {

      console.log(fieldData, this.normalizeFormData(fieldData))
      
      this.post(`/sites/${this.site.key}/config/${key}`, this.normalizeFormData(fieldData))
        .then(res => {

          localStorage.removeItem(`@config/${this.site.key}/config/${key}`)

          resolve(res)

        })

    })
  }

}

const ConfigService = new ConfigServiceClass()

export default ConfigService;