import React, { Component } from 'react'
import { Select } from 'evergreen-ui'

class FormSelect extends Component {
  constructor(props)
  {
    super(props)
    this.handleChange = this.handleChange.bind(this)
  }
  handleChange(event)
  {
    this.props.onChange(this.props.field.key, event.target.value)
  }
  render() {
    return (
      <div className="form-block__field">
        <label className="form-block__field-label">{this.props.field.label}</label>
        {(this.props.field.description?(
          <small className="form-block__field-describe">{this.props.field.description}</small>
        ):null)}
        <Select onChange={this.handleChange}>
          {this.props.field.options?this.props.field.options.map((opt, index) => (
            <option key={index} value={opt}>
              {opt}
            </option>
          )):null}
        </Select>
      </div>
    )
  }
}

export default FormSelect;
