import React, { Component } from 'react'
import { Table, Text } from 'evergreen-ui'

class TableCmp extends Component {
  constructor(props){
    super(props)
    this.state = {
      emptyMsg: 'No Results',
      ...props
    }
  }
  render() {
    return (
      <Table>
      {
        ('cols' in this.props)?(
          <Table.Head>
            {this.props.cols.map((col, index) => (<Table.TextHeaderCell key={col.key}>{col.label}</Table.TextHeaderCell>))}
          </Table.Head>
        ):null
      }
      <Table.Body>
        {
          (('rows' in this.props && this.props.rows.length)?(
            this.props.rows.map((row, index) => (
              <Table.Row key={index} height="auto" isSelectable>
                {this.props.cols.map(col => (
                  <Table.TextCell key={col.key} height={50}>{row[col.key]}</Table.TextCell>)
                )} 
              </Table.Row>
            ))
          ):(<Table.Row height={50}><Table.TextCell><Text color="muted">{this.state.emptyMsg}</Text></Table.TextCell></Table.Row>))
        }
        
      </Table.Body>
      </Table>
    )
  }
}

export default TableCmp;
