import React, { Component } from 'react'
import { Switch } from 'evergreen-ui'

class FormBoolean extends Component {
  constructor(props)
  {
    super(props)
    this.handleChange = this.handleChange.bind(this)
  }
  handleChange(event)
  {
    this.props.field.onChange(this.props.field.key, !this.props.field.value)
  }
  render() {
    return (
      <div>
        <label className="form-block__field-label">
          <Switch height={20}
            checked={this.props.field.value}
            onChange={this.handleChange}
            hasCheckIcon={true}
          />
          <span className="field-label__content">{this.props.field?.label}</span>
        </label>
        {this.props.field.description?(
          <small className="form-block__field-describe">{this.props.field?.description}</small>
        ):null}
      </div>
    )
  }
}

export default FormBoolean;
