import React, { Component } from 'react'
import FormField from './FormField'

class FormKeys extends Component {
  constructor(props)
  {
    super(props)
    this.handleChange = this.handleChange.bind(this)
  }
  handleChange(field, value)
  {
    this.props.field.onChange(this.props.field.key, { ...this.props.field.value, [field]: value })
  }
  render() {
    return (
      <div className="form-block__field">
        <label className="form-block__field-label">{this.props.field.label}</label>
        {(this.props.field.description?(
          <small className="form-block__field-describe">{this.props.field.description}</small>
        ):null)}
        <ul className="form-block__field-list --key-list">
          {Object.keys(this.props.field.keys).map((key, index) => (
            <li key={index}>
              <FormField field={{
                key: key,
                errors: this.props.field.errors,
                label: key,
                placeholder: this.props.field.keys[key]?.placeholder,
                type: this.props.field.keys[key].type,
                onChange: this.handleChange,
                value: undefined === this.props.field.value ? ( "default" in this.props.field.keys[key] ? this.props.field.keys[key].default : undefined ) : ( key in this.props.field.value ? this.props.field.value[key] : undefined ),
                description: this.props.field.keys[key]?.description
              }}/>
            </li>
          ))}
        </ul>
      </div>
    )
  }
}

export default FormKeys;
